import Card from '@/components/common/Card.vue'
import CommodityList from '@/components/common/GoodList.vue'
	import UniNumberBox from '@/components/uni-number-box/uni-number-box.vue'
	import $http from '@/common/api/request.js'
export default {
	components:{
		Card,
		CommodityList,
		UniNumberBox
	},
	data() {
		return {
		    isShow:false,
			animationData:{},
			goodsContent:{},
			swiperList:[
				{imgUrl:"../../static/img/details1.jpeg"},
				{imgUrl:"../../static/img/details2.jpeg"},
				{imgUrl:"../../static/img/details3.jpeg"}
			],
			dataList:[
				{
					id:1,
					imgUrl:"../../static/img/commodity1.jpg",
					name:"大姨绒毛大款2020年必须买,不买你就不行了,爆款疯狂GG008大姨绒毛大款2020年必须买,不买你就不行了,爆款疯狂GG008",
					pprice:"299",
					oprice:"659",
					discount:"5.2"
				},
				{
					id:2,
					imgUrl:"../../static/img/commodity2.jpg",
					name:"大姨绒毛大款2020年必须买,不买你就不行了,爆款疯狂GG008大姨绒毛大款2020年必须买,不买你就不行了,爆款疯狂GG008",
					pprice:"299",
					oprice:"659",
					discount:"5.2"
				},
				{
					id:3,
					imgUrl:"../../static/img/commodity3.jpg",
					name:"大姨绒毛大款2020年必须买,不买你就不行了,爆款疯狂GG008大姨绒毛大款2020年必须买,不买你就不行了,爆款疯狂GG008",
					pprice:"299",
					oprice:"659",
					discount:"5.2"
				},
				{
					id:4,
					imgUrl:"../../static/img/commodity4.jpg",
					name:"大姨绒毛大款2020年必须买,不买你就不行了,爆款疯狂GG008大姨绒毛大款2020年必须买,不买你就不行了,爆款疯狂GG008",
					pprice:"299",
					oprice:"659",
					discount:"5.2"
				}
			]
		}
	},
	onLoad(e) {
		this.getdetailData(e.id)
	},
	// 监听左上角默认返回功能
	onBackPress(event){
		if(this.isShow){
			this.hidePop()
			return true;
		}
	},
	//点击分享 不支持h5
	onNavigationBarButtonTap(e) {
		console.log(e)
		if(e.type==='share'){
			uni.share({
				"provider":"weixin",
				"type":0,
				"scene":"WXSceneSession",
				"title":this.goodsContent.name,
				"href":"http://192.168.8.6:8080/#/pages/details/details?id="+this.goodsContent.id+"",
				imageUrl:this.goodsContent.imgUrl,
				success: function (res) {
					uni.showTabBar({
						title:"分享成功"
					})
				},
				fail: function (err) {
					console.log("fail:" + JSON.stringify(err));
				}
				
			})
		}
	},
	methods: {
		//点击购买
		showPop(){
			var animation = uni.createAnimation({
			   duration: 200
			})
			animation.translateY(600).step();
			this.animationData = animation.export();
			this.isShow = true;
			setTimeout(()=>{
				animation.translateY(0).step();
				this.animationData = animation.export();
			},200)
		},
		// 点击蒙层关闭
		hidePop(){
			var animation = uni.createAnimation({
			   duration: 200
			})
			animation.translateY(600).step();
			this.animationData = animation.export();
			this.isShow =true;
			setTimeout(()=>{
				animation.translateY(0).step();
				this.animationData = animation.export();
				this.isShow = false;
			},200)
		},
		getdetailData(id){
			$http.request({
				url: '/goods/id',
				data:{
					id:id
				}
			
			}).then((res) => {
				this.goodsContent = res[0];
			}).catch(() => {
				uni.showToast({
					title: '请求失败',
					icon: 'none'
				})
			})
		}
		
	}
}
